#globalPlacement {
  --neumorph-height-width: 300px;
  --neumorph-borderradius: 30px;
  --neumorph-background: #CAE6E8;
  --neumorph-previewBackground: #CAE6E8;
  --neumorph-boxShadow: 23px 23px 60px #acc4c5, -23px -23px 60px #e8ffff;
  --neumorph-backgroundCSS: "#CAE6E8";
}

#arrangeParent {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  padding: 24px;
  background: var(--neumorph-background);
  --neumorph-transition: width 0.3s cubic-bezier(.47, 1.64, .41, .8), border-radius 0.3s cubic-bezier(.47, 1.64, .41, .8), height 0.3s cubic-bezier(.47, 1.64, .41, .8);
}

#mainBox {
  position: relative;
  width: 499px;
  height: 499px;
}

#configBox {
  position: relative;
  width: 590px;
  height: 600px;
  padding: 30px;
  padding-left: 80px;
}

.relativeLayer {
  position: relative;
  height: 100%;
  width: 100%;
}

.configChildRow {
  width: 80%;
  margin-bottom: 0px;
  margin-top: 0px;
  padding: 0;
  text-wrap: nowrap;
}

.insideConfigBox {
  background: var(--neumorph-previewBackground);
  box-shadow: var(--neumorph-boxShadow);
  flex-direction: column;
  width: 99%;
  height: 98%;
  border-radius: 30px;
  padding: 30px 0;
}

.insideTargetBox {
  height: var(--neumorph-height-width);
  width: var(--neumorph-height-width);
  transition: var(--neumorph-transition);
  border-radius: var(--neumorph-borderradius);
  background: var(--neumorph-previewBackground);
  box-shadow: var(--neumorph-boxShadow);
}

svg {
  stroke: var(--neumorph-background);
}

#ClipboardCSS {
  color: var(--neumorph-background)
}

#ClipboardCSSBackgroundValue:before {
  content: var(--neumorph-backgroundCSS);
}

.wrapCSS {
  width: 100%;
  overflow-wrap: break-word;
}

@media (max-width: 1137px) {
  #arrangeParent {
    padding: 12px;
  }

  #mainBox {
    position: relative;
    width: 400px;
    height: 400px;
  }

  #configBox {
    position: relative;
    width: 450px;
    height: 600px;
  }
}

@media (max-width: 863px) {
  #arrangeParent {
    /* padding: 12px; */
    padding-top: 20px;
    flex-direction: column;
  }

  #mainBox {
    position: relative;
    width: 360px;
    height: 360px;
  }

  #configBox {
    position: relative;
    width: 400px;
    height: 600px;
    padding: 2px;
  }
}

@media (max-width: 593px) {
  #arrangeParent {
    /* padding: 12px; */
    padding-top: 20px;
    flex-direction: column;
    gap: 30px;
  }

  #mainBox {
    position: relative;
    width: 360px;
    height: 360px;
  }

  #configBox {
    position: relative;
    width: 100%;
    height: 600px;
    padding: 0px;
  }
}